import React from "react"
import PropTypes from "prop-types"
import { Link, graphql } from "gatsby"

// Components
import Layout from "../components/layout"
import Sidebar from "../components/sidebar"
import MailList from "../components/maillist"

const Category = ({ pageContext, data }) => {
  const { category } = pageContext
  const { edges, totalCount } = data.allMarkdownRemark
  const categoryHeader = `
  يوجد عدد  
 ( ${totalCount} )
  مقال في قسم
:  
    ${category}
   
    `
  return (
    <Layout>
      <div class="col-md-4" id="content-desktop">
        <Sidebar />
      </div>
      <div class="col-md-8">
        <hr />
        <div class="alert alert-secondary" role="alert">
          <h3 class="my-1">
            {categoryHeader}
            <i class="fa fa-tags fa-sm"></i>
          </h3>
        </div>
        <br />
        <ul dir="rtl">
          {edges.map(({ node }) => {
            const { slug } = node.fields
            const { title } = node.frontmatter
            return (
              <h6>
                <b key={node.id}>
                  <Link to={slug} class="stretched-link text-secondary">
                    <i class="fa fa-tags fa-sm"></i> {title}.
                    <br />
                  </Link>
                </b>
              </h6>
            )
          })}
        </ul>
        <br />
        <Link to="/category" class="btn btn-secondary btn-sm">
          <i class="far fa-arrow-alt-circle-left"></i> جميع الأقسام
        </Link>
        <br />
        <br />
        <div id="content-mobile">
          <div class="p-3 mb-2 bg-success text-white">القائمة البريدية</div>
          <MailList />
        </div>
        <hr />
      </div>
    </Layout>
  )
}

Category.propTypes = {
  pageContext: PropTypes.shape({
    category: PropTypes.string.isRequired,
  }),
  data: PropTypes.shape({
    allMarkdownRemark: PropTypes.shape({
      totalCount: PropTypes.number.isRequired,
      edges: PropTypes.arrayOf(
        PropTypes.shape({
          node: PropTypes.shape({
            frontmatter: PropTypes.shape({
              title: PropTypes.string.isRequired,
              category: PropTypes.string.isRequired,
            }),
            fields: PropTypes.shape({
              slug: PropTypes.string.isRequired,
            }),
          }),
        }).isRequired
      ),
    }),
  }),
}

export default Category

export const pageQuery = graphql`
  query($category: String) {
    allMarkdownRemark(
      limit: 2000
      sort: { fields: [frontmatter___date], order: DESC }
      filter: { frontmatter: { category: { in: [$category] } } }
    ) {
      totalCount
      edges {
        node {
          fields {
            slug
          }
          frontmatter {
            title
            category
          }
        }
      }
    }
  }
`
